<template>
     <v-fab-transition>
        <v-speed-dial
        v-model="fab"
        bottom right fixed
        :direction="direction"
        :open-on-hover="hover"
        :transition="transition"
        class="mb-5"
        >
            <v-btn
                slot="activator"
                v-model="fab"
                color="blue darken-2"
                dark
                fab
                @click="fab = true"
            ><v-icon size="35px">reorder</v-icon><v-icon>close</v-icon>
            </v-btn>
            <v-btn
                color="red darken-2"
                dark
                fab
                small
                @click="onLoved"
            ><v-icon>{{icon}}</v-icon>
            </v-btn>
            <v-btn
                fab
                dark
                color="green"
                small
            >
            {{noOfLikes}}
            </v-btn>
            <v-btn
                fab
                dark
                color="indigo"
                small
                @click="onContact"
            >
                <v-icon>fas fa-comment</v-icon>
            </v-btn>
        </v-speed-dial>
    </v-fab-transition>
</template>

<script>
import axios from 'axios';
export default {
    props: ["loved"],
    data() {
        return {
            love: this.loved,
            noOfLikes: 0,
            fab: false,
            direction: 'top',
            hover: true,
            transition: 'slide-y-reverse-transition'
        }
    },
    mounted() {
        this.hidden = false;
    },
    computed:{
        icon() {
            if(this.love === 'true') {
                return 'fas fa-heart';
            }
            else {
                return 'far fa-heart'
            }
        }
    },
    methods: {
        onLoved() {
            if(this.love === 'true') {
                axios.post("https://afternoon-brook-10335.herokuapp.com/like", {loved: 'true'})
                .then((response) => {
                    this.noOfLikes = response.data.loves;
                    this.love = 'false';
                    this.$cookie.set('loved', 'false');
                });
            }
            else {
                axios.post("https://afternoon-brook-10335.herokuapp.com/like", {loved: 'false'})
                .then((response) => {
                    this.noOfLikes = response.data.loves;
                    this.love = 'true';
                    this.$cookie.set('loved', 'true');
                });
            }
        },
        onContact() {
            this.$emit("contact");
        }
    },
    created() {
        axios.get("https://afternoon-brook-10335.herokuapp.com/likes")
        .then((response) => {
        this.noOfLikes = response.data.likes;
        });
    }
}
</script>
