<template>
  <v-card>
    <v-bottom-nav
      class="bottomnav"
      :active.sync="bottomNav"
      :color="color"
      :value="true"
      dark
      app
      fixed
    >
      <v-btn v-for="link in links" 
      :to="link.to" 
      :key="link.title"
      class="font-weight-bold"
      dark>
        <span>{{link.title}}</span>
        <v-icon>{{link.icon}}</v-icon>
      </v-btn>
    </v-bottom-nav>
  </v-card>
</template>

<script>
    export default {
        data () {
        return {
            bottomNav: 4,
            links: [
                {to:"/", title:"About", icon:"fa-user"},
                {to:"/skills", title:"Skills", icon:"fa-tools"},
                {to:"/education", title:"Education", icon:"fa-graduation-cap"},
                {to:"/projects", title:"Projects", icon:"fa-tasks"}
            ]
        }
        },

        computed: {
            color () {
                switch (this.bottomNav) {
                case 0: return '#3f51b5'
                case 1: return '#54ac56'
                case 2: return '#3f51b5'
                case 3: return 'teal'
                default: return '#3f51b5'
                }
            }
        }
    }
</script>
<style scoped>
.bottomNav{
    bottom: 0;

}

</style>


