<template>
  <v-app>
    <v-content class="mt-2 mb-5 pa-1"> 
      <v-snackbar v-model="snack" :timeout="4000" top color="success">
            <span>Awesome! Your feedback has been sent</span>
            <v-btn flat color="white" @click="snack=false">Close</v-btn>
        </v-snackbar>
      <Dialog :dialog="dialog" @dialogClose="dialog = false"/>
      <ContactPopup :contact="contact" @sent="onMessageSent" @close="contact=false"/>
      <transition name="fadeUp">
        <router-view></router-view>
      </transition>
    </v-content>
    <BottomNav/>
    <SpeedDial :loved="loved" @contact="contact = true"/>
  </v-app>
</template>

<script>
import BottomNav from './components/BottomNav.vue';
import SpeedDial from './components/SpeedDial.vue';
import Dialog from './components/Dialog.vue';
import ContactPopup from './components/ContactPopup.vue';
import axios from 'axios';
require('vue2-animate/dist/vue2-animate.min.css');
export default {
  components: {
    BottomNav,
    SpeedDial,
    Dialog,
    ContactPopup
  },
  data() {
    return {
      loved: 'false',
      likes: 0,
      dialog: false,
      contact: false,
      snack:false
    };
  },
  created() {
    if(!this.$cookie.get('ipAdd')) {
      this.dialog = true;
      axios.get("https://ipinfo.io/json/?token=4c204b06d692c4")
      .then((response) => {
        this.$cookie.set('ipAdd', response.data.ip, 2000);
        return axios.post("https://afternoon-brook-10335.herokuapp.com/newIp", {ip: response.data.ip})
      })
      .then((response) => {
      })
    }
    if(this.$cookie.get('loved') === 'true') {
      this.loved = 'true'
    }
  },
  methods: {
    onMessageSent() {
      this.contact = false;
      this.snack = true;
    }
  }
}
</script>

<style>
#app{
  background: #FFFAFA;
}
</style>
