<template>
    <v-dialog persistent max-width="600px" v-model="contact">
        <v-card>
            <v-card-title>
                <h2>Contact me</h2>
                <v-spacer></v-spacer>
                <v-btn 
                flat
                @click="onClose"
                color="blue"
                >Close
                </v-btn>
            </v-card-title>
            <v-card-text>
                <v-form class="px-3" ref="form">
                    <v-text-field label="Name" v-model="name" :rules="inputRules" prepend-icon="fa-user-alt"></v-text-field>
                    <v-text-field label="Email" v-model="email" :rules="inputRules" prepend-icon="fa-envelope-square"></v-text-field>
                    <v-text-field label="Feedback" v-model="feedback" :rules="inputRules" prepend-icon="edit"></v-text-field>
                    <v-spacer></v-spacer>
                    <v-btn class="success mx-0 mt-3" @click="submit" :loading="loading">Send</v-btn>
                </v-form>
            </v-card-text>
        </v-card>
    </v-dialog>  
</template>
<script>
import axios from 'axios';
export default {
    props: ["contact"],
    data() {
        return {
            name: '',
            email: '',
            feedback: '',
            inputRules: [
                v => v.length != 0 || "Field cant be empty"
            ],
            loading: false
        }
    },
    methods : {
        submit() {
            if(this.$refs.form.validate()) {
                this.loading = true;
                const message = {
                    name: this.name,
                    email: this.email,
                    feedback: this.feedback,
                }
                axios.post("https://afternoon-brook-10335.herokuapp.com/message", message)
                .then((response) => {
                    this.loading = false;
                    this.$emit("sent");
                    this.name = '';
                    this.email = '';
                    this.feedback = '';
                });
            }
        },
        onClose() {
            this.$emit('close');
        }
    }
}
</script>
