<template>
    <v-dialog
      v-model="dialog"
      persistent
      max-width="290"
    >
      <v-card>
        <v-card-title class="headline">Welcome to my Portfolio</v-card-title>

        <v-card-text>
          This Portfolio has been built with <v-icon small>fas fa-heart</v-icon> in Vue<v-icon color="green" small>fab fa-vuejs</v-icon> and Vuetify.<br/>
          This website uses cookies<v-icon small color="grey">fas fa-cookie</v-icon> to make it even more delicious.
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="green darken-1"
            flat="flat"
            @click="changeDialog"
            @mouseover="changeDisagreeIcon"
            @mouseout="changeDisagreIcon"
          >
            Disagree  <v-icon>{{disagreeIcon}}</v-icon>
          </v-btn>

          <v-btn
            color="green darken-1"
            flat="flat"
            @click="changeDialog"
            @mouseover="changeAgreeIcon"
            @mouseout="changeAgreIcon"
            >
            Agree  <v-icon>{{agreeIcon}}</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
</template>

<script>
export default {
    props :["dialog"],
    data() {
        return {
           agreeIcon: "far fa-meh",
           disagreeIcon: "far fa-dizzy"
        };
    },
    methods: {
        changeDialog(){
          this.$emit('dialogClose');
        },
        changeAgreeIcon() {
            this.agreeIcon = 'far fa-smile';
        },
        changeAgreIcon() {
            this.agreeIcon = 'far fa-meh';
        },
        changeDisagreeIcon() {
            this.disagreeIcon = 'far fa-angry';
        },
        changeDisagreIcon() {
            this.disagreeIcon = 'far fa-dizzy';
        }
    }
}
</script>

    