<template>
    <v-container fluid>
        <v-card>
            <v-card-title class="main-card">
                <div class="white--text mx-auto">
                    <v-icon class="white--text" left>fa-tools</v-icon>
                    <span class="display-1 font-weight-light">Skills</span>
                </div>
            </v-card-title>
            <v-card-text>
                <v-layout row wrap justify-space-around>
                    <v-flex 
                    md3 
                    sm6 
                    xs12
                    v-for="skill in skills"
                    :key="skill.title"
                    >
                        <template>
                            <v-hover>
                                <v-card 
                                class="text-xs-center skill-card ma-3 evaluation-2"
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 4}`"
                                :color="skill.background">
                                    <v-card-title>
                                        <div class="mx-auto">
                                            <v-progress-circular
                                            :value="skill.value"
                                            :color="skill.color"
                                            :rotate="-90"
                                            width="15"
                                            size="100"
                                            >
                                            {{skill.value}}
                                            </v-progress-circular>
                                        </div>
                                    </v-card-title>
                                    <v-card-actions>
                                        <v-spacer></v-spacer>
                                        <v-layout justify-space-around>
                                        <div class="headline" >
                                            <v-tooltip bottom >
                                                <v-btn outline 
                                                slot="activator"
                                                :color="skill.color" 
                                                :class="`${skill.color}--text`"
                                                >
                                                <v-icon small>{{skill.icon}}</v-icon>{{skill.title}}
                                                </v-btn>
                                                <span>Duration: {{skill.duration}}</span>
                                            </v-tooltip>
                                        </div>
                                        <v-btn icon @click="skill.show = !skill.show">
                                            <v-icon>{{ skill.show ? 'keyboard_arrow_up' : 'keyboard_arrow_down' }}</v-icon>
                                        </v-btn>
                                        </v-layout>
                                    </v-card-actions>
                                    <v-slide-y-transition>
                                    <v-card-text class="white black--text" v-show="skill.show">
                                        {{skill.details}}
                                    </v-card-text>
                                    </v-slide-y-transition>
                                </v-card>
                            </v-hover>
                        </template>
                    </v-flex>
                </v-layout>
            </v-card-text>
        </v-card>
    </v-container>
</template>

<script>
export default {
    data() {
        return {
            skills: [
                {title:"Javascript", value:0, absoluteValue: 60, color:"indigo", background: "#d6f6ff", icon: "fab fa-js-square", show: false, duration: "2 years", details: "JavaScript often abbreviated as JS, is a high-level, interpreted programming language that conforms to the ECMAScript specification. It is a language that is also characterized as dynamic, weakly typed, prototype-based and multi-paradigm."},
                {title:"Python", value:0, absoluteValue: 80, color:"white", background: "#0894a1", icon: "fab fa-python", show: false, duration: "3 years", details: "Python is an interpreted, high-level, general-purpose programming language. Created by Guido van Rossum and first released in 1991"},
                {title:"Node.js", value:0, absoluteValue: 70, color:"#424242", background: "#00EEFA", icon: "fab fa-node", show: false, duration: "1 year", details: "Node.js is an open-source, cross-platform JavaScript run-time environment that executes JavaScript code outside of a browser. It is a JavaScript runtime built on Chrome's V8 JavaScript engine."},
                {title:"Vue.js", value:0, absoluteValue: 80, color:"red", background: "#68EFBF", icon: "fab fa-vuejs", show: false, duration: "1 year", details: "Vue is a progressive framework for building user interfaces."},
                {title:"Flutter", value:0, absoluteValue: 40, color:"blue", background: "#F2EFDF", icon: "fab fa-android", show: false, duration: "6 months", details: "Flutter is Google's mobile UI framework for crafting high-quality native experiences on iOS and Android in record time."},
                {title:"MongoDB", value:0, absoluteValue: 60, color:"#000000", background: "#F5DFFB", icon: "fas fa-database", show: false, duration: "1 year", details: "MongoDB is a cross-platform document-oriented database program. Classified as a NoSQL database program, MongoDB uses JSON-like documents with schemata."},
                {title:"MySQL", value:0, absoluteValue: 50, color:"cyan", background: "#1B1A28", icon: "fas fa-database", show: false, duration: "3 years", details: "MySQL is an open source relational database management system (RDBMS)"}
            ],
            interval: {},
        }
    },
    beforeDestroy () {
      clearInterval(this.interval)
    },
    mounted () {
      var timesRun = 0;
      this.interval = setInterval(() => {
          if(timesRun === 10) {
              clearInterval(this.interval);
              return;
          }
          timesRun += 1;
        this.skills.forEach((skill) => {
            if(skill.absoluteValue !== skill.value) {
                skill.value += 10
            }
        });
      }, 300)
    }
}
</script>

<style scoped>
.main-card {
    background: #54ac56;
}
</style>
